body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #222;
  margin: 0;
  color: #ddd;
  font-size: 1.5em;
  font: normal 1.5em Verdana;
}

a {
  color: white;
  text-decoration: none;
}

.layout {
  margin: 1em auto;
}

.menu {
  display: flex;
  justify-content: space-between;
  margin: 1em 1em 0.5em;
  padding: 0.5em 1em;
  background: #8883;
}

.graph {
  position: absolute;
  width: 100%;
  height: 100%;
}
